* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Rubik", "Pacifico", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 21px;
  color: #000;
}

/* Global classes */

.wrapper {
  max-width: 1520px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.clear {
  clear: both;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin-top: -1px;
  margin-left: -1px;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  border: none;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

.btnSignUp {
  width: 355px;
  height: 74px;
  border: none;
  background-color: #E50058;
  border-radius: 60px;
  color: #fcfcfc;
  font-weight: bold;
  font-size: 24px;
  display: block;
  margin: 0 auto;
  margin-bottom: 35px;
}

.btnInsta {
  width: 355px;
  height: 74px;
  border: none;
  background: linear-gradient(90deg, #fed46d, #f4833f, #f15838, #eb1c61, #d5218f, #974499, #4359a8);
  border-radius: 60px;
  color: #fcfcfc;
  font-weight: bold;
  font-size: 24px;
  display: block;
  margin: 0 auto;
}

.sectionForm {
  display: flex;
  justify-content: center;
  padding-bottom: 115px;
}

.sectionForm label {
  font-size: 18px;
  color: #161616;
}

.sectionForm__h {
  text-align: center;
  color: #E50058;
  font-weight: bold;
  font-size: 36px;
  margin-bottom: 40px;
}

.sectionForm__form_input {
  max-width: 550px;
  width: 550px;
  height: 62px;
  border: 1px solid #bdbdbd;
  border-radius: 5px;
  padding-left: 25px;
  margin-top: 19px;
  margin-bottom: 38px;
}

/* Atomic classes */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

/* General */

.navigation {
  display: flex;
  justify-content: space-between;
  margin-top: 56px;
}

.navigation__ul {
  list-style-type: none;
}

.navigation__li {
  margin-bottom: 30px;
}

.navigation__logotype_img {
  width: 234px;
}

.navigation__menuBtn {
  width: 34px;
  height: 25px;
  position: relative;
  margin-left: 11px;
  cursor: pointer;
}

.navigation__menuBtn:before {
  content: "";
  position: absolute;
  display: block;
  top: 50%;
  background-color: #333333;
  width: 27px;
  height: 4px;
  border-radius: 4px;
  left: 0;
  transform: translate(0, -50%);
}

.navigation__menuBtn:after {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  background-color: #333333;
  width: 34px;
  height: 4px;
  border-radius: 4px;
  left: 0;
  transform: translate(0, -50%);
}

.navigation__menuBtn_bottom {
  position: absolute;
  width: 34px;
  height: 4px;
  border-radius: 4px;
  background-color: #333333;
  bottom: -2px;
}

.navigation__joinNow_btn {
  width: 155px;
  height: 50px;
  text-transform: uppercase;
  border-radius: 60px;
  border: none;
  background-color: #E50058;
  color: #fcfcfc;
  font-size: 18px;
}

.firstSection {
  padding-bottom: 135px;
  background-image: url(../img/svg/FirstScreenLeftImg.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 5% 0%;
  position: relative;
}

.firstSection:after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  height: 726px;
  width: 650px;
  background-image: url(../img/svg/FirstScreenRightImg.svg);
  background-repeat: no-repeat;
  background-size: contain;
}

.firstSection__scrollDown {
  width: 200px;
  text-align: center;
  position: relative;
  font-size: 24px;
  cursor: pointer;
}

.firstSection__scrollDown:before {
  content: "";
  position: absolute;
  top: 40px;
  right: 50%;
  width: 38px;
  height: 23px;
  background-image: url("../img/Arrow Right.png");
  background-size: cover;
  transform: translate(50%, -50%);
}

.firstScreenBlock {
  text-align: center;
  padding-bottom: 117px;
}

.firstScreenBlock__center {
  display: flex;
  justify-content: center;
}

.firstScreenBlock__logotype {
  margin-bottom: 40px;
}

.firstScreenBlock__logotype_img {
  width: 143px;
}

.firstScreenBlock__h {
  font-size: 96px;
  margin-bottom: 59px;
}

.firstScreenBlock__p {
  font-family: Pacifico;
  font-size: 48px;
  line-height: 30px;
  color: #333333;
  margin-bottom: 49px;
}

.firstScreenBlock__p_medium {
  font-size: 48px;
  font-family: Rubik;
  font-weight: bold;
  color: #333333;
  margin-bottom: 35px;
}

.firstScreenBlock__p_end {
  font-size: 36px;
  color: #828282;
  max-width: 870px;
  line-height: 40px;
}

.SectionToPpl {
  display: flex;
  justify-content: center;
  padding-bottom: 160px;
}

.SectionToPpl__h {
  font-size: 48px;
  font-family: Pacifico;
  color: #161616;
  margin-bottom: 50px;
  text-align: center;
}

.SectionToPpl__logotype {
  margin: 0 auto;
  width: 450px;
  margin-bottom: 30px;
}

.SectionToPpl__p {
  text-align: center;
  font-size: 18px;
  color: #4f4f4f;
  line-height: 25px;
  max-width: 515px;
  margin: 0 auto;
  margin-bottom: 55px;
}

.SectionToPpl__ul {
  list-style-type: none;
}

.SectionToPpl__li {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(16.666666666666668% - 30px);
  margin: 0 auto;
}

.SectionToPpl__li_p {
  font-size: 26px;
  color: #333333;
  line-height: 30px;
  margin-bottom: 80px;
}

.SectionToPpl__endText {
  text-align: center;
  font-size: 18px;
  color: #4f4f4f;
  width: 515px;
  margin: 0 auto;
}

.sectionHelp {
  padding-bottom: 85px;
}

.sectionHelp__h {
  font-size: 64px;
  color: #161616;
  font-weight: bold;
  margin-bottom: 60px;
  text-align: center;
}

.sectionHelp__p {
  color: #E50058;
  font-size: 36px;
  line-height: 16px;
  font-family: Pacifico;
  text-align: center;
  margin-bottom: 150px;
}

.sectionHelp__ul {
  list-style-type: none;
  margin: 0 auto;
}

.sectionHelp__li {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.333333333333336% - 30px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sectionHelp__li_img {
  margin: 0 auto;
  text-align: center;
}

.sectionHelp__li_h {
  text-align: center;
  width: 350px;
  font-size: 24px;
  font-weight: bold;
  color: #000000;
  margin-bottom: 20px;
}

.sectionHelp__li_p {
  text-align: center;
  width: 350px;
  font-size: 24px;
  color: #828282;
}

.sectionForm__form_input::-webkit-input-placeholder {
  font-size: 18px;
  font-weight: regular;
  color: #bdbdbd;
}

.sectionForm__form_input:-ms-input-placeholder {
  font-size: 18px;
  font-weight: regular;
  color: #bdbdbd;
}

.sectionForm__form_input::-ms-input-placeholder {
  font-size: 18px;
  font-weight: regular;
  color: #bdbdbd;
}

.sectionForm__form_input::placeholder {
  font-size: 18px;
  font-weight: regular;
  color: #bdbdbd;
}

.whySignUp {
  background-image: url(../img/BackgroundGrey.jpg);
  padding-bottom: 170px;
  position: relative;
}

.whySignUp:before {
  content: "";
  position: absolute;
  top: -170px;
  left: 0;
  background-image: url(../img/svg/GrayScreenLeft.svg);
  height: 470px;
  width: 650px;
  background-repeat: no-repeat;
  background-size: contain;
}

.whySignUp:after {
  content: "";
  position: absolute;
  top: 0px;
  right: 0;
  background-image: url(../img/svg/GrayScreenRight.svg);
  height: 811px;
  width: 350px;
  background-repeat: no-repeat;
  background-size: contain;
}

.whySignUp__h {
  padding-top: 120px;
  text-align: center;
  max-width: 700px;
  width: 700px;
  margin: 0 auto;
  font-size: 36px;
  font-weight: bold;
  color: #333333;
  line-height: 30px;
  margin-bottom: 110px;
}

.whySignUp__ul {
  list-style-type: none;
  margin-bottom: 60px;
}

.whySignUp__li {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.333333333333336% - 30px);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.whySignUp__li_img {
  margin-bottom: 20px;
}

.whySignUp__li_p {
  max-width: 367px;
  width: 367px;
  font-size: 24px;
  text-align: center;
  line-height: 30px;
  color: #333333;
}

.whySignUp__circle {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 165px;
  width: 165px;
  border-radius: 50%;
  background-color: #E50058;
  margin: 0 auto;
}

.whySignUp__circle_p {
  text-align: center;
  line-height: 100px;
  color: #ffffff;
  font-size: 18px;
  box-shadow: 10px rgba(0, 0, 0, 0.3);
}

.whySignUp__circle_pi {
  margin-top: -20px;
  color: #ffffff;
  font-size: 36px;
}

.motivation {
  display: flex;
  justify-content: center;
  background-image: url(../img/BackgroundRose.jpg);
  padding-bottom: 150px;
  position: relative;
}

.motivation:before {
  content: "";
  position: absolute;
  opacity: 0.5;
  bottom: -25px;
  left: 0;
  background-image: url(../img/svg/RoseImageLeft.svg);
  height: 470px;
  width: 650px;
  background-repeat: no-repeat;
  background-size: contain;
}

.motivation:after {
  content: "";
  position: absolute;
  top: 120px;
  right: 0;
  opacity: 0.5;
  background-image: url(../img/svg/RoseImageRight.svg);
  height: 811px;
  width: 510px;
  background-repeat: no-repeat;
  background-size: contain;
}

.motivation__h {
  padding-top: 170px;
  font-size: 36px;
  font-weight: 600;
  color: #ffffff;
  text-align: center;
  max-width: 486px;
  width: 486px;
  margin: 0 auto;
  margin-bottom: 95px;
  line-height: 30px;
}

.motivation__ul {
  display: flex;
  justify-content: center;
  list-style-type: none;
}

.motivation__li {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.333333333333336% - 30px);
}

.motivation__li_p {
  text-align: center;
  font-size: 24px;
  color: #ffffff;
  font-family: Rubik;
  width: 320px;
  line-height: 25px;
}

.sectionFormTwo {
  padding-top: 92px;
}

.sectionFormTwo__p {
  text-align: center;
  width: 536px;
  margin: 0 auto;
  font-size: 24px;
  line-height: 25px;
  color: #828282;
  margin-bottom: 45px;
}

.footer {
  background-image: url(../img/footerBackground.jpg);
  padding-top: 100px;
  position: relative;
}

.footer__footerBox {
  display: flex;
  align-items: flex-start;
}

.footer ul {
  list-style-type: none;
  align-self: flex-start;
}

.footer__logotype {
  margin-right: calc(8.333333333333334% + 15px);
}

.footer__ul {
  margin-right: calc(8.333333333333334% + 15px);
  margin-top: 30px;
  align-self: flex-end;
  color: #fcfcfc;
}

.footer__ul a {
  color: #fcfcfc;
  text-decoration: none;
}

.footer__li_h {
  margin-bottom: 38px;
}

.footer__li_p {
  margin-bottom: 28px;
}

.footer__scrollTop {
  position: absolute;
  bottom: 145px;
  right: 200px;
  color: #FCFCFC;
}

.footer__scrollTop:before {
  content: "";
  position: absolute;
  top: -20px;
  right: 50%;
  width: 25px;
  height: 15px;
  background-image: url(../img/svg/Arrow Right.svg);
  background-size: cover;
  transform: translate(50%, -50%);
}

.footer__AllRight {
  background-color: #7C004D;
  line-height: 40px;
  text-align: center;
  font-size: 14px;
  color: #FCFCFC;
}

@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}